<template>
  <q-card style="width: 500px; max-width: 80vw; border-radius: 15px">
    <q-toolbar class="bg-secondary text-white padding-3y padding-6x">
      <q-icon size="md" name="icon-settings-02" />
      <q-toolbar-title>Server Configuration</q-toolbar-title>
    </q-toolbar>
    <q-form @submit="submitSettings" :ref="form_conf.ref" class="card-padding">
      <q-input
        outlined
        autofocus
        clearable
        color="secondary"
        placeholder="API Base URL"
        :input-class="form_conf.inputclass"
        v-model="server.host"
        :rules="[
          (val) => checker.input({ type: 'url', value: val, nomsg: true }, 500),
        ]"
      >
        <template v-slot:prepend>
          <q-icon
            size="xs"
            name="icon-server-05"
            class="hover-text-secondary"
          />
        </template>
      </q-input>
      <q-slide-transition :duration="1000">
        <q-list v-show="input_host_info.success" class="q-pt-md">
          <q-item
            class="q-my-sm hover-text-secondary"
            v-show="input_host_info.api.withSSL"
          >
            <q-item-section avatar>
              <q-icon name="icon-shield-tick" size="sm" />
            </q-item-section>
            <q-item-section>
              <q-item-label>Secure Server (HTTPS)</q-item-label>
              <q-item-label caption lines="1"
                >All requests are encrypted</q-item-label
              >
            </q-item-section>
          </q-item>

          <q-item
            class="q-my-sm hover-text-secondary"
            v-show="input_host_info.api.host"
          >
            <q-item-section avatar>
              <q-icon name="icon-server-01" size="sm" />
            </q-item-section>
            <q-item-section>
              <q-item-label>{{ input_host_info.api.host }}</q-item-label>
              <q-item-label caption lines="1">Hostname</q-item-label>
            </q-item-section>
          </q-item>

          <q-item
            class="q-my-sm hover-text-secondary"
            v-show="input_host_info.api.port"
          >
            <q-item-section avatar>
              <q-icon name="icon-cube-01" size="sm" />
            </q-item-section>
            <q-item-section>
              <q-item-label>{{ input_host_info.api.port }}</q-item-label>
              <q-item-label caption lines="1">Port</q-item-label>
            </q-item-section>
          </q-item>
        </q-list>
      </q-slide-transition>

      <div class="row q-pt-md">
        <q-space />
        <q-btn
          no-caps
          unelevated
          color="secondary"
          class="rounded slide-hover text-body1 font-medium button-md q-px-md"
          :loading="validating.status"
          :disable="!input_host_info.success || validating.status"
          :percentage="validating.percentage"
          @click="submitValidation"
          style="width: 150px"
        >
          <!-- <q-icon name="icon-3-balls" size="xs" class="on-left" /> -->
          Validate

          <template v-slot:loading>
            <q-spinner-ball
              size="xs"
              class="on-left"
              style="animation-play-state: paused"
            />
            Verifying
          </template>
        </q-btn>
      </div>
    </q-form>
  </q-card>
</template>

<script>
import { defineComponent } from 'vue';
import { useQuasar } from 'quasar';
import { checker, tryClearInterval } from 'src/assets/scripts/functions';
import fnStore from 'src/stores/fn.store';

export default defineComponent({
  name: 'ServerSetting',

  data() {
    return {
      $q: useQuasar(),
      checker,
      server: {
        host: null,
      },
      loginAnimate: null,
      interval: null,
      request_response: null,
      validating: { status: false, percentage: 0 },
      form_conf: { ref: 'update_server_form', inputclass: 'form_active_input' },
    };
  },

  emits: ['success', 'failed', 'close'],

  watch: {
    request_response: {
      async handler(val) {
        if (val) {
          tryClearInterval(this.loginAnimate);
          setTimeout(() => {
            this.validating.status = false;
          }, 500);
        }
      },
    },
  },

  computed: {
    input_host_info() {
      // Analyze Server
      try {
        const url = new URL(this.server.host);
        const withSSL = Boolean(url.protocol.includes('https') || 0);
        const api = {
          withSSL,
          protocol: url.protocol,
          host: url.hostname,
          port: url.port ? Number(url.port) : '',
        };
        return { success: api.host ? 1 : 0, api, url };
      } catch (e) {
        return { success: 0, api: {}, url: {} };
      }
    },
  },

  methods: {
    submitValidation() {
      const elem = this.$refs?.[this.form_conf.ref];
      if (elem) elem.submit();
    },

    async submitSettings() {
      this.processAnimation();
      this.request_response = null;
      // Send Request to API
      const response = await this.testServerHost();
      this.request_response = response?.data || {};
      const success = this.request_response.success;
      if (success) {
        if (this.server.host?.endsWith('/'))
          this.server.host = this.server.host.slice(0, -1);
        try {
          const { success, api, url } = this.input_host_info;
          if (success) {
            // Save Settings
            fnStore.setAPIHost(this.server.host);
          } else {
            // unexpected error
            this.$emit('failed', this.request_response, this.server);
            return;
          }
        } catch (e) {
          if (process.env.DEV_MODE)
            console.error('Failed Saving Settings:', e.message);
          this.request_response = {
            success: 0,
            error: { message: 'Failed Saving Settings' },
          };
        }
      }
      // Emit Response
      if (process.env.DEV_MODE)
        console[success ? 'info' : 'error'](
          'API Test',
          success ? 'Success' : 'Failed',
          ':',
          this.request_response,
          this.server
        );
      this.$emit(
        success ? 'success' : 'failed',
        this.request_response,
        this.server
      );
    },

    async testServerHost() {
      try {
        const url = `${this.server.host}/test-connect`;
        return await this.$api.get(url, { withCredentials: false });
      } catch (e) {
        if (process.env.DEV_MODE) console.error('Server Error:', e.message);
        return { success: 0, message: e.message };
      }
    },
    async processAnimation(callback) {
      this.validating.status = true;
      this.loginAnimate = setInterval(() => {
        this.validating.percentage += Math.floor(Math.random() * 8 + 10);
        if (this.validating.percentage >= 100) {
          if (callback) callback();
          this.validating.status = false;
          tryClearInterval(this.loginAnimate);
        }
      }, 100000);
    },
  },

  mounted() {
    this.server.host = this.$config.api.baseURL;
  },

  beforeUnmount() {
    tryClearInterval(this.loginAnimate);
    this.$emit('close');
  },
});
</script>
